.ttu { text-transform: uppercase; }
.ttl { text-transform: lowercase; }

/* SLIDESHOW */
.cycle-slideshow {
	width: 100%; position: relative;
}

.cycle-pager {
	width: 100%; text-align: center;
	span {
		border: 2px solid #fff; .border-radius(20px); height: 20px; width: 20px; display: inline-block; cursor:pointer;
		margin: 0 3px; line-height: 14px; font-size: 36px; color: #fff; overflow: hidden; text-indent: -500px; .transition(0.2s);
		&:hover, &:focus { .transition(none); }
	}
	span:hover, span:focus, .cycle-pager-active { text-indent: 0; }
}

/* OTHER */

/* google maps mw */
.gm-style img { max-width: inherit; }

/* responsive video */
.video-container {
	position: relative; padding: 0 0 56.25%; height: 0; overflow: hidden; margin: 0 0 15px;

	iframe {
		position: absolute; top:0; left: 0; width: 100%; height: 100%;
	}
}

/* equal height */
.equal-h {
	.flex-box; .flex-box-wrap; overflow: hidden;
	&:before, &:after { display: none; }
}

html.no-flexbox {
	.equal-h {
		height: 100%;
		> div { float: left; }
	}
}

/* vertical align */
.va-wrap, .va-m, .va-b { display: table; width: 100%; height: 100%; }
.va-wrap { table-layout: fixed; }
.va-m, .va-b { display: table-cell; }
.va-m { vertical-align: middle; }
.va-b { vertical-align: bottom; }

/* blocklink */
.blocklink { cursor: pointer; }


/* =============================================================================
   HEADER
   ========================================================================== */
.page-header {

}

.logo {
	text-decoration: none; .opacity(100);
	img { display: block; }
	&:hover, &:focus { .opacity(80); }
}

.main-nav {
	text-transform: uppercase; .optimize-font; color: @black;

	a {
		text-decoration: none; color: @grayDark; padding: 9px 0 9px 45px;
		display: block; border-top: 1px solid @grayLight;
	}
	a:hover, a:focus, li.active a { color: @black; }

	.trigger {
		display: block; position: relative; cursor: pointer;
		padding: 9px 0 9px 45px; font-weight: 500;
		white-space: nowrap; overflow: hidden; text-overflow: ellipsis;
		.icon-hamburger { position: absolute; left: 0; top: 12px; }
	}

	.nav-wrap {  display: none; margin: 0 0 15px; }

	@media @tablet {
		li { float: left; margin: 0 0 0 20px; }
		a { border-top: 0; padding: 0; }
		.trigger { display: none; }
		.nav-wrap { margin: 0 0 0 -20px; display: block; }
	}
}


/* =============================================================================
   MAIN
   ========================================================================== */

.main {
	padding: 40px 0;

	@media @tablet { padding: 50px 0 150px; }
}

.button_default {
  background-color: @accent;.transition;
  &:hover { background-color: @accentHover;}
}

.button_easein {
  background-color: @accent;.transition(0.7s, easeInOut);
  &:hover { background-color: @accentHover;}

}
.button_easeout {
  background-color: @accent;.transition(0.7s, easeOut);
  &:hover { background-color: @accentHover;}

}

/* =============================================================================
   FOOTER
   ========================================================================== */

.page-footer {
	position: relative;
	.row { margin-bottom: 0; }

	@media @tablet {
		height: 60px; margin: -60px 0 0; z-index: 5;
	}
}

a.esign {
	width: 104px; height: 20px; .at2x('../images/logo-esign.png', '../images/logo-esign@2x.png', 104px, 20px);
	text-indent: -9999px; .opacity(60); .transition(1s); display: block; outline: 0;
    &:hover, &:focus { .opacity(100); }

    @media @tablet { float: right; }
}


/* =============================================================================
   CONTENT
   ========================================================================== */

.large { .fs(16); }

.center-block {
	text-align: center; .clearfix; margin: 20px 0;
	.button { float: none; display: inline-block; }
}

.box { margin: 0 0 20px; padding: 15px 15px 5px; .clearfix; background: #E7E4DB;
	ul, p { margin-bottom: 1em; }
	&.error { background: @error; color: #fff; }
	&.success { background: @success; }
}


section { position: relative; }

article {
	.clearfix; margin: 0 0 20px;

	a {
		img { display: block; .transition(0.2s); .opacity(100); }
		&:hover, &:focus {
			img { .opacity(80); }
		}
	}
}

.map { width: 100%; height: 350px; background: #efefef; margin: 0 0 40px; }

/* modals */

@media @tablet {
  .md-close { display: none;}



}


.page-banner {
  height: 400px; width: 100%;
  .background-size(cover); background-position: center top;
  .at2x('https://placekitten.com/1200/400', 'https://placekitten.com/2400/800')

}


.main-nav .md-modal .logo {@media @tablet {display: none;} }
.mobile {
  .md-modal {
    position: fixed; top: 50%; left: 50%; width: 50%; z-index: 2000;
    min-width: 320px; height: auto; visibility: hidden; .backface-visibility;
    .transform(translateX(-50%) translateY(-50%)); pointer-events: none;
    text-align: left; .fs(18);

    a { padding: 10px 0;}




    @media @mobile {
      display: none;
      &[data-view-slide="true"] { display: block; }
    }

    @media @desktop {
      width: 940px;
    }

    &[data-view-slide="true"] { visibility: visible; }

  }

  .md-cover {
    top: 0; left: 0; width: 100%;
    .transform(none);


    .md-content {
      height: 100%; height: 100vh; overflow-y: scroll;


      .transform(scale(0.9)); .backface-visibility;
      text-align: center;


      .logo {
        display: inline-block; margin-bottom: 20px; padding: 20px 0;
      }
    }

    .md-close {
      position: fixed; z-index: 1000; top: 15px; left: 30px;

      .transform(translateY(20%) scale(0.8)); opacity: 0;
      i { }

    }

    &.md-show {
      .md-close {
        opacity: 1; .transform(translateY(0));

      }
    }

    @media @tablet {
      .md-close {
        top: 4px; right: 28px;
      }
    }
  }

  .md-content.md-content {

    .clearfix;
    position: relative;
    margin: 0 auto;
    padding: 35px 25px;
    background: @accent;
    .transition;
    color: #fff;
    &[data-animation-type="fade"] {
      opacity: 0;
    }
    .transform(translateY(20%));
    &[data-animation-type="from-left"] {
      .transform(translateX(-100%));
    }
    &[data-animation-type="from-top"] {
      .transform(translateY(-100%));
    }

    h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
      color: #fff;
    }

    a {
      color: #fff; border-top: 0;
      &:hover, &:focus {
        color: #efefef;
      }
    }

    .md-close, .md-close-holder {
      position: absolute;
      top: 4px;
      left: 12px;
    }

    @media @tablet {
      padding: 40px 45px;
    }
  }

  .md-show {
    visibility: visible;
    pointer-events: auto;

    .md-content {

      //padding-left: 75px;
      &[data-animation-type="fade"] {
        opacity: 1;
      }
      .transform(translateY(0));
      &[data-animation-type="from-left"] {
        .transform(translateX(0%));
      }
      &[data-animation-type="from-top"] {
        .transform(translateY(0%));
      }

    }

    ~ .md-overlay {
      .opacity(100);
      visibility: visible;
    }

    @media @mobile {
      display: block;
    }
  }


  .md-modal .nav-wrap {
    display: block;
    margin: 0;
    list-style: none;
    text-align: center;
    li {
      margin-bottom: 20px;
    }
  }

  // add close icon
  .md-close {
    width: 35px;
    height: 35px;
    line-height: 35px;
    background: @accentHover;
    text-align: center;
    padding: 0;
    .fs(20);
  }

  .md-overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
    visibility: hidden;
    .opacity(0);
    background: rgba(0, 0, 0, 0.2);
    .transition;
  }
}

.noscroll {
  overflow: hidden; }

.floating-label--container {
  position: relative;
  display: inline-block;vertical-align: top;

  label {
    .fs(14);
    position: absolute;
    top:0; left: 0; .transform(translate(0,0));
    white-space: nowrap; width: auto;
    opacity: 0; visibility: hidden;
    .transition(all 0.5s);

    &.active {
      display: block; visibility: visible; opacity: 1;.fs(12);
      .transform(translate(0.5em, 0.5em));


    }
  }

  input {
    .border-box;
    height: 55px;

    &.active {
      padding-top: 20px;
    }
  }

  &.floating-label--container_above {
    margin-top: 1.4em;
    label.active { .transform(translate(0em, -2em));  }
    input { height: 35px; &.active { padding-top: 6px;}}
  }

  &.floating-label--container_error {
    input {
      border: 1px solid red;
    }

  }

}

// margin to fix negative margin of fieldset ul
.floating-label--container label {
  margin-left: 20px;
}



