		content: ""; display: block;
		position: absolute; bottom: 0; left: 0;
		border: 8px solid transparent; border-bottom: 0; border-top-color: @accent;
	}
}

.formError .formErrorArrowBottom {
	.box-shadow(none); margin: 0px 0 0 12px; top:2px;
}

